.Badges__hero {
  width: 100%;
  padding: 2rem 0;
  background: url('../../assets/stars.svg'), #1B1B25;
  background-repeat: repeat;
  margin-bottom: 1rem;
  color: #ffffff;
  .Badges__container {
    width: 100%;
    max-width: 580px;
    margin: 0 auto;
    padding: 0 1rem;
    .title {
      font-family: 'Mali', cursive;
      color: #fff;
      font-weight: 600;
      font-size: 2.5rem;
      text-align: center;
      text-decoration: none;
      text-shadow: 0px 0px 5px #aaa;
    }
  }
  &>.Badges__container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
